  color: #555555;
  background-color: #f2f4f5;
  overflow-x: hidden; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

p {
  margin: 0; }

ul {
  padding: 0;
  margin: 0; }

h1,
h2,
