  .skills__list-item {
    max-width: unset; } }

.projects__list {
  list-style: none; }

.projects__list-item {
  position: relative;
  display: flex;
  flex-direction: row;
  margin-bottom: 80px;
  font-size: 16px; }
  .projects__list-item:last-child {
    margin-bottom: 0; }

.projects__image-wrapper {
  position: absolute;
  left: 0;
  top: 0;
  width: 250px;
  height: 333px;
  margin-right: 100px;
  overflow: hidden;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1); }

.projects__image-link {
  display: block;
