  height: 333px; }

.projects__image-link:first-child,
.projects__image-link:last-child {
  transition: transform 1s; }

.projects__image-wrapper:hover > .projects__image-link:first-child,
.projects__image-wrapper:hover > .projects__image-link:last-child {
  transform: translateY(-100%); }

.projects__description {
